body,
html,
#root {
  height: 100%;
  background-image: url('./background_photo_desktop.webp');
  background-position: center;
  background-repeat: no-repeat;
  justify-content: center;
}

html {
  font-size: 18px;
}

h1 {
  margin-top: 2%;
  border: .25rem #004C54 solid;
  border-radius: 1.25rem;
  box-shadow: black 0.0625rem 0.0625rem 0.125rem 0.125rem;
  padding: .77rem 0;
  background-color: rgba(0, 0, 0, 0.9);
  text-align: center;
  font-family: 'Poppins', sans-serif;
  font-size: 1.88rem;
  color: #fff;
}

h2 {
  font-family: 'Poppins', sans-serif;
  font-size: 1.55rem;
  padding: 10px 0;
  text-decoration: underline;
}

p {
  margin: 3px 0;
  padding: 1px 0;
}

.App {
  height: 100%;
  padding: 0 10% 0 10%;

  font-family: 'Work Sans', sans-serif;
  font-weight: 500;
  color: rgb(0, 0, 0);  
  text-align: center;
}

.userTeam{
  display: flex;

}

