.UserTeam {
    min-width: 50%;
    height: 450px;
    padding: 0 0 50px 0;
    overflow-y: scroll;
    background-color: #011c1fe8;
    box-shadow: 0 4px 2px 2px #000000;
    font-size: larger;
    color: white;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    border-top-left-radius: 10px;
  }

  
  ::-webkit-scrollbar {
      width: 0px;
      background: transparent;
  }
  


