@media only screen and (max-width: 1200px) {
  nav a {
    width: 100%;

  } 


  }


nav {
  width: 100%;
  
}
nav a{
  color: white;
  border: .25rem #004C54 solid;
  border-radius: 1.25rem;
  box-shadow: black 0.0625rem 0.0625rem 0.125rem 0.125rem;
  font-size: 1.5rem;
  text-decoration: none;
  padding: 0.75rem;
  background-color: black;
}

nav a:hover {
  box-shadow: 0.125rem 0.125rem 0.3125rem 0.3125rem;
  color: white;
  border: .25rem black solid;
  background-color: black;
  opacity: 100%;
}


.teamNav{
  justify-content: center;

  font-size: 1.5rem;
  text-decoration: none;
  padding: 1.75rem;

  opacity: 90%;
  display: flex;
  flex-wrap: wrap;
}


